@import 'vars';
html {
    width: 100%;
    font-size: 100vw;
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: #fff;
    background: $black;
    font-size: 0.833334%;
    leading-trim: both;
    text-edge: cap;
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
    }
}

.container {
    width: 100%;
    margin: 0 auto;
    padding: 0 4.375em 0 14.375em;
}

a {
    &:hover {
        color: $accent;
    }
}


@media screen and (max-width: 992px){
    .page{
        font-size: 2.2%;
        padding: 6.75em 0 7.25em;
    }

    .container{
        padding: 0 1.25em;
    }
}


@media screen and (max-width: 576px){
    .page{
        font-size: 3.72093%;
    }
}
