@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 .9375em;
    height: 2.8125em;
    border-radius: .3125em;
    leading-trim: both;
    text-edge: cap;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.3125em;
        line-height: 138%;
    }
}

.m-btn-big{
    padding: 0 2.1875em;
    height: 4.375em;
    span{
        font-size: 1.5em;
    }
    img,
    svg{
        width: 1.25em;
        height: auto;
        margin-right: .9375em;
    }
}

.m-btn-accent{
    color: #fff;
    background: $accent;
    position: relative;
    &:before{
        content: '';
        position: absolute;
        border-radius: inherit;
        inset: 0;
        background: rgba(103, 87, 206, 0.5);
        filter: blur(17.5px);
    }
    &:hover{
        color: #fff;
        background: darken($accent,10%);
    }
}

.m-btn-light{
    color: $darkGray;
    background: rgba(125, 127, 146, 0.25);
    &:hover{
        color: #fff;
        background: $accent;
    }
}

.m-btn-light-outline{
    color: $darkGray;
    border: .125em solid $darkGray;
    &:hover{
        color: #fff;
        background: $accent;
        border-color: $accent;
    }
}

.m-btn-blur{
    position: relative;
    border: none;
    &:hover{
        &:before{
            border-color: $accent;
        }
    }
    &:before{
        content: '';
        position: absolute;
        border-radius: inherit;
        inset: 0;
        background: rgba(125, 127, 146, 0.25);
        border: .09375em solid rgba(149, 148, 155, 0.65);
        backdrop-filter: blur(5px);
    }
    img,
    svg,
    span{
        position: relative;
        z-index: 2;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 2.6875em;
        span{
            font-size: 1.125em;
        }
    }
}
