.film-item{
  padding: 6.25em 0;
  position: relative;
  height: 100%;
  z-index: 2;
  &__inner{
    display: flex;
    justify-content: space-between;
    max-width: 90.4375em;
  }
  &__img{
    width: 19.5em;
    margin-left: 2em;
    flex-shrink: 0;
    padding-top: 5.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  .film-detail{
    display: block;
    max-height: unset;
  }
  .ratings{
    margin: 0 .9375em 0 0;
    &__item{
      &-img{
        height: 2.8125em;
      }
    }
  }
  &__info{
    display: flex;
    align-items: center;
    margin-top: 3em;
  }
  .film-detail__desc{
    max-width: 46em;
  }
  .film-detail__img{
    position: fixed;
  }
  &__nav{
    display: flex;
    flex-wrap: wrap;
    margin-top: 7em;
  }
  &__btn{
    margin: 1.875em 1.875em 0 0;
  }
  &__next{
    width: 2.5em;
    animation: move-arrow 2s infinite alternate;
    img{
      width: 100%;
    }
    &-wrap{
      width: 100%;
      display: flex;
      justify-content: center;
      position: absolute;
      left: 0;
      bottom: 1.875em;
    }
  }
}

.tags{
  display: flex;
  align-items: center;
}

.tag{
  @include lastMr(.9375em);
}

@keyframes move-arrow {
  0% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(.625em);
  }
  50% {
    transform: translateY(0);
  }
  75% {
    transform: translateY(-.625em);
  }
  100% {
    transform: translateY(0);
  }
}

.film-info{
  padding: 7.5em 0;
  &__group{
    @include lastMb(5.625em);
  }
  &__buttons{
    display: flex;
    flex-wrap: wrap;
    margin-top: 1.25em;
  }
  &__btn{
    height: 4.1875em;
    margin: 1.25em 1.25em 0 0;
    border-radius: .625em;
  }
}

.actors{
  &__info{
    margin-top: 1.125em;
  }
}

.actor-slider{
  .swiper{
    &-container{
      margin-top: 1.875em;
      width: 14.375em;
    }
    &-slide{
      padding-right: 1.875em;
      &-active{
        .actor-card__img{
          &:after{
            opacity: 1;
          }
        }
      }
    }
  }
}

.actor-card{
  &__img{
    @include imgCover(100%);
    border-radius: 1.5625em;
    margin-bottom: 1.25em;
    img{
      border-radius: inherit;
    }
    &:after{
      border-radius: inherit;
      content: '';
      position: absolute;
      inset: 0;
      z-index: 3;
      transition: all .3s;
      opacity: 0;
      border: .25em solid #FFFFFF;
    }
  }
}

.actor-detail{
  display: none;
  &._active{
    display: block;
  }
  .film-detail__props{
    margin-top: .25em;
  }
}


@media screen and (max-width: 992px){
  .film-page{
    .film-detail__img{
      position: absolute;
      height: $fullHeight;
      &:before{
        background: linear-gradient(0deg, rgba(10, 6, 20, 0.75), rgba(10, 6, 20, 0.75));
      }
      &:after{
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 3.125em;
        background: $black;
        filter: blur(12.5px);
      }
    }
  }

  .film-item{
    padding: 0 0 4.375em;
    &__inner{
      display: block;
    }
    &__next,
    &__img{
      display: none;
    }
    &__info{
      margin-top: 1em;
      display: block;
    }
    .ratings{
      &__item{
        @include lastMr(.9375em);
        &-img{
          height: 1.875em;
        }
        &-title{
          font-size: 1.3125em;
        }
      }
    }
    .tags{
      margin-top: 1.25em;
    }
    .film-detail__props{
      margin-top: 2.25em;
    }
    &__nav{
      margin-top: 2.75em;
    }
    &__btn{
      margin: 0 1.5652em 0 0;
      padding: 0;
      width: 3.75em;
      height: 3.75em;
      img{
        margin: 0;
      }
      span{
        display: none;
      }
    }
  }

  .film-info{
    padding: 4.375em 0 1.25em;
    &__group{
      @include lastMb(2.375em);
    }
    &__buttons{
      margin: 0;
    }
    &__btn{
      height: 3.3125em;
      padding: 0 1.25em;
    }
  }

  .actors{
    padding-bottom: 2.25em;
    .text64{
      font-size: 2.25em;
    }
    &__info{
      margin-top: .875em;
    }
  }

  .actor-slider{
    .swiper{
      &-container{
        margin-top: 1.5em;
      }
      &-slide{
        width: 9.375em;
        padding-right: 1.25em;
      }
    }
  }

  .actor-detail{
    .film-detail__props{
      margin-top: .375em;
      display: block;
    }
    .film-detail__prop{
      &:before{
        display: none !important;
      }
      @include lastMb(.25em);
    }
  }
}
