.text82{
  font-size: 5.125em;
  line-height: 136.5%;
  letter-spacing: 0.03em;
}

.text64{
  font-size: 4em;
  line-height: 136%;
}

.text48{
  font-size: 3em;
  line-height: 136%;
}

.text32{
  font-size: 2em;
  line-height: 136%;
}

.text24{
  font-size: 1.5em;
  line-height: 137.5%;
}

.text21{
  font-size: 1.3125em;
  line-height: 138%;
}

.semi-bold-text{
  font-weight: 600;
}

.bold-text{
  font-weight: 700;
}

.dark-gray-text{
  color: $darkGray;
}


@media screen and (min-width: 993px){
  @media screen and (max-height: 1080px){
    .text82{
      font-size: 4em;
    }

    .text64{
      font-size: 3.5em;
    }
  }
  @media screen and (max-height: 1000px){
    .text82{
      font-size: 3.5em;
    }

    .text64{
      font-size: 3em;
    }

    .text48{
      font-size: 2.5em;
    }
  }
  @media screen and (max-height: 860px){
    .text24{
      font-size: 1.25em;
    }
    .text21{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 992px){
  .text82{
    font-size: 2.25em;
  }

  .text64{
    font-size: 2em;
  }

  .text48,
  .text32{
    font-size: 1.5em;
  }

  .text24{
    font-size: 1.125em;
  }
}
