.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 2.8125em 0;
  z-index: 3;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__user{
    width: 4.375em;
    height: 4.375em;
    display: block;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }
  &__share,
  &__group,
  &__notification,
  &__full{
    margin-right: 3em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__notification{
    position: relative;
    width: 2em;
    .pin{
      position: absolute;
      right: .125em;
      top: .125em;
      width: .5625em;
      height: .5625em;
      border-radius: 50%;
      background: $red;
      display: block;
      &:before{
        content: '';
        @include centerAbsolute;
        border-radius: 50%;
        background: $red;
        filter: blur(2px);
      }
    }
  }
  &__group{
    width: 2.4375em;
  }
  &__share{
    width: 1.875em;
  }
  &__full{
    width: 2.3125em;
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: .9375em 0;
    &__user{
      width: 3.125em;
      height: 3.125em;
    }
    &__share,
    &__group,
    &__notification,
    &__full{
      margin-right: 2em;
    }
    &__full{
      width: 2.125em;
    }
    &__share{
      width: 1.75em;
    }
    &__group{
      width: 2.25em;
    }
    &__notification{
      width: 1.8125em;
    }
    &._open{
      .mob-menu{
        opacity: 1;
        pointer-events: unset;
        transform: translateX(0);
      }
      .mob-menu__bg{
        display: block;
      }
    }
  }

  .burger{
    width: 2.375em;
    height: 2.375em;
    border-radius: 50%;
    @include center;
    background-color: $accent;
    margin-right: auto;
    img{
      width: 1.25em;
    }
  }
}
