.type-nav{
  position: absolute;
  z-index: 4;
  top: 3.75em;
  left: 14.375em;
  display: flex;
  &__btn{
    margin-right: .9375em;
    &._active{
      background: $accent;
      color: #fff;
      pointer-events: none;
    }
  }
}

.films{
  height: 100%;
  .container{
    height: 100%;
  }
  &__inner{
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 8.125em 0 2em;
  }
}

.films-slider{
  margin-top: auto;
  padding-top: 2em;
  padding-bottom: 3.75em;
  .swiper{
    &-container{
      margin-top: 2em;
      width: 38em;
    }
    &-wrapper{
      align-items: flex-end;
    }
    &-slide{
      width: 35em;
      padding-right: 4.375em;
      &-next,
      &-prev{
        .film-card{
          &__badge,
          &__img{
            &:before{
              opacity: .55;
            }
          }
        }
      }
      &-active{
        width: 100%;
        .film-card{
          &__badge{
            height: 3em;
          }
          &__badge,
          &__img{
            &:before{
              opacity: 0;
            }
            &:after{
              opacity: 1;
            }
          }
        }
      }
    }
  }
  @media screen and (min-width: 993px){
    &._type2{
      padding-bottom: 0;
      .swiper{
        &-container{
          width: 19.75em;
        }
        &-slide{
          width: 18.3125em;
          padding-right: 3.125em;
        }
      }

      .film-card{
        &__img{
          padding-bottom: 144%;
        }
      }
    }
  }
}

.film-card{
  transition: all .3s;
  &__img,
  &__badge{
    &:after,
    &:before{
      border-radius: inherit;
      content: '';
      position: absolute;
      inset: 0;
      z-index: 3;
      transition: all .3s;
    }
  }
  &__badge{
    min-width: 7.5em;
    transition: all .3s;
    &:before{
      opacity: .75;
      background: #000;
    }
    &:after{
      display: none;
    }
  }
  &__img{
    @include imgCover(56.2%);
    border-radius: .3125em;
    img{
      border-radius: inherit;
      transition: all .3s;
    }
    &:after{
      opacity: 0;
      border: .25em solid #FFFFFF;
    }
    &:before{
      opacity: .75;
      background: #000;
    }
  }
  &__badge{
    position: absolute;
    top: 1.625em;
    left: -1.25em;
    background-color: $yellow;
    padding: 0 1.0625em;
    height: 2.625em;
    z-index: 4;
    border-radius: .3125em;
    @include center;
    font-weight: 600;
    color: $black;
    span{
      font-size: 1.25em;
      line-height: 135%;
    }
  }
  &__content{
    display: none;
  }
}

.film-detail{
  max-height: 26em;
  overflow: hidden;
  display: none;
  &._active{
    display: block;
  }
  &__img{
    position: absolute;
    inset: 0;
    z-index: -1;
    &:before{
      content: '';
      position: absolute;
      inset: 0;
      background: linear-gradient(63.96deg, rgba(10, 6, 20, 0.75) 51.5%, rgba(10, 6, 20, 0.4125) 70.21%);
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__props{
    display: flex;
    margin-top: 1.5em;
  }
  &__prop{
    display: flex;
    align-items: center;
    &:not(:first-child){
      &:before{
        content: '';
        flex-shrink: 0;
        display: block;
        height: 1.0625em;
        width: .125em;
        background-color: #fff;
        margin: 0 1.125em;
      }
    }
  }
  &__desc{
    margin-top: 2.25em;
    max-width: 45em;
    .text21{
      @include truncate(5);
    }
  }
  .ratings{
    margin-top: 1.5em;
  }
}

.ratings{
  display: flex;
  align-items: center;
  &__item{
    display: flex;
    align-items: center;
    &:not(:last-child){
      margin-right: 1.875em;
    }
    &-img{
      height: 2.5em;
      flex-shrink: 0;
      margin-right: .9375em;
      img{
        height: 100%;
        width: auto;
      }
    }
  }
}


@media screen and (min-width: 993px){
  @media screen and (max-height: 1080px){
    .films-slider{
      padding-bottom: 0;
      &._type2{
        .swiper{
          &-container{
            width: 15.5em;
          }
          &-slide{
            width: 14em;
            padding-right: 2em;
          }
        }
      }
    }
  }

  @media screen and (max-height: 1000px){
    .film-detail{
      .ratings,
      &__props,
      &__desc{
        margin-top: 1em;
      }
    }

    .films-slider{
      .swiper{
        &-container{
          margin-top: 1.5em;
          width: 29em;
        }
        &-slide{
          width: 27em;
          padding-right: 2em;
        }
      }
      &._type2{
        .swiper{
          &-container{
            width: 13em;
          }
          &-slide{
            width: 12em;
            padding-right: 1.5em;
          }
        }
      }
    }
  }

  @media screen and (max-height: 860px){
    .film-detail{
      &__desc{
        max-width: 54em;
        .text21{
          -webkit-line-clamp: 3;
        }
      }
    }

    .films-slider{
      padding-top: 1em;
      .swiper{
        &-container{
          width: 25em;
        }
        &-slide{
          width: 24em;
          padding-right: 1.5em;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .type-nav{
    display: none;
  }

  .films{
    &__inner{
      padding: 0;
    }
    &__info{
      display: none;
    }
  }

  .films-slider{
    padding: 0;
    .swiper{
      &-container{
        width: 100%;
        margin: 1.75em 0 0;
      }
      &-slide{
        width: 15.9375em;
        padding-right: .9375em;
      }
    }
    &._type2{
      .swiper{
        &-slide{
          width: 9.6875em;
        }
      }
      .film-card{
        &__img{
          padding-bottom: 142%;
        }
      }
    }
  }

  .film-card{
    &__img{
      padding-bottom: 67%;
      &:before,
      &:after{
        display: none;
      }
    }
    &__badge{
      display: none;
    }
    &__content{
      display: block;
      margin-top: .875em;
    }
    &__desc{
      font-size: .875em;
      line-height: 136%;
      margin-top: .5em;
    }
  }
}
