@import "_header";
@import "_footer";
.layout-slider{
  width: 100%;
  height: $fullHeight;
  overflow: hidden;
  &__container{
    height: $fullHeight;
  }
  &__pagination{
    right: 1.6875em !important;
    .swiper-pagination-bullet{
      width: .9375em;
      height: .3125em;
      opacity: 1;
      background: $muted;
      border-radius: .25em;
      margin: 1.25em 0 !important;
      &-active{
        background: #FFF;
      }
    }
  }
}

.mob-menu{
  &__date{
    margin-right: 3em;
  }
  &__bg{
    display: none;
  }
}

.footer-menu,
.menu{
  position: fixed;
  top: 50%;
  left: 1.875em;
  transform: translateY(-50%);
  height: calc(100 * var(--vh) - 5em);
  width: 7.8125em;
  z-index: 100;
  padding: 1.25em .25em;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  &:before{
    content: '';
    position: absolute;
    inset: 0;
    background: rgba(125, 127, 146, 0.25);
    backdrop-filter: blur(12.5px);
    z-index: -1;
    border-radius: 2.1875em;
  }
  &__logo{
    width: 3.4375em;
    margin-bottom: 4.25em;
    img{
      width: 100%;
    }
  }
  &__link,
  &__links{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__link{
    font-weight: 600;
    color: $muted;
    position: relative;
    &._active,
    &:hover{
      color: #fff;
      circle,
      path{
        stroke: #fff;
      }
      &:before{
        opacity: 1;
      }
    }
    &:before{
      content: '';
      position: absolute;
      top: -3.625em;
      left: 50%;
      transform: translateX(-50%);
      width: 9.375em;
      height: 9.375em;
      border-radius: 50%;
      background: rgba(105, 82, 255, 0.45);
      filter: blur(50px);
      z-index: -1;
      transition: all .3s;
      opacity: 0;
    }
    &:not(:last-child){
      margin-bottom: 2.75em;
    }
    span{
      font-size: 1em;
      line-height: 137.5%;
      leading-trim: both;
      text-edge: cap;
      letter-spacing: 0.03em;
    }
    svg{
      width: 2em;
      height: auto;
      margin-bottom: .875em;
    }
  }
}

.footer-menu{
  display: none;
}


@media screen and (min-width: 993px){
  .menu{
    &:hover{
      .menu__link{
        span{
          opacity: 1;
        }
      }
    }
    &__link{
      span{
        transition: all .3s;
        opacity: 0;
      }
    }
  }

  @media screen and (max-height: 1000px){
    .menu{
      height: calc(100 * var(--vh) - 2.5em);
      &__logo{
        margin-bottom: 2em;
      }
      &__link{
        @include lastMb(1.75em);
        svg{
          margin-bottom: .5em;
          width: 1.75em;
        }
        span{
          font-size: .875em;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .layout-slider{
    height: unset;
    &__container{
      height: unset;
    }
    &__wrapper{
      display: block;
      transform: unset !important;
    }
    &__slide{
      height: auto !important;
      @include lastMb(1.75em);
    }
    &__pagination{
      display: none;
    }
  }

  .mob-menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 15.625em;
    height: $fullHeight;
    padding: 1.5em 1.25em;
    overflow-y: auto;
    opacity: 0;
    pointer-events: none;
    transition: all .4s;
    transform: translateX(-200%);
    z-index: 100;
    &:before{
      content: '';
      position: absolute;
      inset: 0;
      background: rgba(23, 21, 31, 0.75);
      backdrop-filter: blur(12.5px);
      z-index: -1;
    }
    &__date{
      margin: 0;
      text-align: center;
    }
    &__bg{
      position: fixed;
      right: 0;
      top: 0;
      height: $fullHeight;
      width: 100%;
      z-index: 99;
    }
  }

  .menu{
    position: static;
    height: unset;
    width: unset;
    transform: unset;
    margin-top: 3.125em;
    display: block;
    padding: 0;
    &__logo,
    &:before{
      display: none;
    }
    &__links{
      align-items: flex-start;
    }
    &__link{
      flex-direction: row;
      svg{
        width: 1.5em;
        margin: 0 .875em 0 0;
      }
      span{
        font-size: 1.125em;
      }
      &:before{
        display: none;
      }
    }
  }

  .footer-menu{
    display: flex;
    align-items: center;
    position: fixed;
    top: unset;
    transform: unset;
    bottom: .625em;
    width: calc(100% - 1.875em);
    left: .9375em;
    height: 5.3125em;
    flex-direction: row;
    padding: 0 2em;
    overflow: hidden;
    border-radius: .9375em;
    &:before{
      border-radius: .9375em;
      backdrop-filter: blur(20px);
    }
    &__links{
      flex-direction: row;
      justify-content: space-between;
      width: 100%;
      margin: 0;
    }
    &__link{
      margin: 0 !important;
      &:before{
        filter: blur(25px);
        width: 6em;
        height: 6em;
        top: 50%;
        transform: translate(-50%,-50%);
      }
      svg{
        margin: 0;
        width: 1.75em;
      }
    }
  }
}
